.button {
    padding: 5px 24px;
    color: #fff;
    border-radius: 3px;
    border: none;
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 0.875rem;
    cursor: pointer;
}

.disabled {
    color: #002E5C66;
    background-color: #004D9966;
    cursor: not-allowed;
}

.secondary {
    background-color: transparent;
    border: 2px solid #004D99;
    color: #004D99;
}

.default {
    background-color: #0066CC;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background-color: #0080FF;
}

.red:hover {
    background-color: #FF1A47;
}

.secondary:hover {
    background-color: #E5F0FA;
}


.default:active {
    background-color: #0059B2;
}

.secondary:active {
    background-color: #C9E5FF;
}

.red:active {
    background-color: #B21232;
}

.button:disabled {
    color: #002E5C66;
    background-color: #004D9966;
}

