.radio {
    appearance: none;
    border: 1px solid #333;
    border-radius: 50%;
    width: 20px;
    height: 20px;
    position: relative;
}

.radio::before {
    position: absolute;
    content: '';
    display: block;
    width: 13px;
    height: 13px;
    background-color: #000;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%) scale(0);
    transition: transform .2s ease-in;
}

.radio:checked {
}

.radio:checked::before {
    transform: translate(-50%, -50%) scale(1);
}

.label {
    display: flex;
}

.options {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.span {
    font-family: Montserrat, sans-serif;
    font-size: 1rem;
    font-weight: 400;
    margin-left: 10px;
}
