.select {
    width: 165px;
    padding: 2px 0 5px 10px;
    border: 1px solid #C2C2C2;
    cursor: pointer;
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    text-align: left;

}

.select:focus {
    outline: 1px solid #333;
}

.option:hover {
    background-color: #C2C2C2;
}
