[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    padding: 8px 11px 10px;
    border: 1px solid var(--color);
    border-radius: 2px;
    font-family: Montserrat, sans-serif;
    font-size: 0.875rem;
    background-color: var(--background);
    color: var(--color);
}
