.sidebar {
    position: absolute;
    left: -320px;
    width: 320px;
    height: 100%;
    z-index: 99;
    background: #FFF;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.10), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
    transition: left .2s ease-in;
}

.sidebar.open {
    left: 0;
}

.background {
    background-color: rgba(0 0 0 / .7);
    position: absolute;
    inset: 0;
    z-index: 10;
}

.nav {
    display: flex;
    flex-direction: column;
    gap: 45px;
    height: 100%;
    padding-left: 70px;
}

.nav a {
    color: #000;
    font-family: Montserrat, sans-serif;
    font-size: 1.375rem;
    font-weight: 600;
}

.nav a:hover {
    color: #58ACFF;
}

.nav a.active {
    color: #0059B2;
}

.close {
    cursor: pointer;
    margin: 16px 10px 24px auto;
    background-color: transparent;
    border: none;
    display: block;
}

